.task-bar {
	--font-size: 20px;
	--task-bar-height: 8%;
	--small-gap: 10px;

	display: flex;
	justify-content: space-evenly;
	align-items: center;

	width: 100%;
	height: var(--task-bar-height);

	box-sizing: border-box;
	margin-bottom: 10px;

	border-radius: 5px;
	border-color: var(--bluey);
	border-width: 1px;
	border-style: solid;

	padding: var(--small-gap);
	gap: var(--small-gap);

	font-family: sans-serif;
	font-size: var(--font-size);
	color: var(--white);
}

.task-name {
	flex-grow: 1;

	border-radius: 5px;
	padding: 10px;

	text-align: left;
	letter-spacing: 0.5px;

	background-color: var(--semi-light-gray);
}

/*I hid the checkbox and make the label the visible "checkbox" instead since clicking the label also toggles the checkbox*/
input[type="checkbox"] {
	position: absolute;
	left: -100vw;
}

.checkbox {
	--approximate-height: calc(var(--font-size) + (2 * var(--small-gap)));
	width: var(--approximate-height);
	height: var(--approximate-height);

	border-radius: 5px;
	border-color: var(--bluey);
	border-width: 1px;
	border-style: solid;

	position: relative;

	background-color: var(--semi-light-gray);
}

.checkbox-content {
	display: block;
	position: absolute;

	left: 50%;
	top: 50%;

	color: var(--bluey);

	transform: translate(-50%, -50%);

	opacity: 0;
	transition: opacity 100ms ease-in-out;
}

.checked-checkbox {
	opacity: 1;
}
